import React, { Fragment, useState } from 'react';
import { globalStyles, styWrapper } from './styles';

function RegistModal({ isOpen, onClose }) {
  const [formData, setFormData] = useState({
    event: 'bride', // 'bride' hoc 'groom'
    name: '',
    quantity: 1
  });
  const [submitting, setSubmitting] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSubmitting(true);

    try {
      // c d liu c t localStorage trc
      let registrations = [];
      const localStorageData = localStorage.getItem('registrations');
      if (localStorageData) {
        try {
          registrations = JSON.parse(localStorageData);
          if (!Array.isArray(registrations)) {
            registrations = [];
          }
        } catch (parseError) {
          console.warn('Li parse localStorage:', parseError);
          registrations = [];
        }
      }

      // Thm d liu mi
      const newRegistration = {
        id: Date.now(),
        name: formData.name.trim(),
        event: formData.event,
        eventName: formData.event === 'bride' ? 'Nh Gi' : 'Nh Trai',
        quantity: parseInt(formData.quantity),
        timestamp: new Date().toISOString(),
        date: new Date().toLocaleString('vi-VN')
      };

      registrations.push(newRegistration);

      // Lu vo localStorage (append, khng replace)
      localStorage.setItem('registrations', JSON.stringify(registrations));
      
      alert(`Cm n ${formData.name}  ng k tham d!`);
      
      // Reset form
      setFormData({
        event: 'bride',
        name: '',
        quantity: 1
      });
      
      // ng modal
      onClose();
    } catch (error) {
      console.error(' Li submit form:', error);
      alert('C li xy ra, vui lng th li!');
    } finally {
      setSubmitting(false);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  if (!isOpen) return null;

  return (
    <Fragment>
      <div css={[styWrapper, globalStyles]} style={{ position: 'fixed', top: 0, left: 0, right: 0, bottom: 0, zIndex: 999, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
        <div className="overlay" onClick={onClose} style={{ cursor: 'pointer' }} />
        <div className="container" style={{ position: 'relative', zIndex: 1000, maxWidth: '500px', margin: '0 auto' }}>
          <div className="row">
            <div className="col-md-12 text-center fh5co-heading" style={{ marginBottom: '30px' }}>
              <h2 className="main-font__regist">ng k tham d</h2>
            </div>
          </div>

          <div className="row">
            <div className="col-md-12">
              <form onSubmit={handleSubmit} className="regist-form">
                <div className="form-group">
                  <label htmlFor="event">Tham gia</label>
                  <select
                    id="event"
                    name="event"
                    className="form-control"
                    value={formData.event}
                    onChange={handleChange}
                    required
                  >
                    <option value="bride">Nh Gi</option>
                    <option value="groom">Nh Trai</option>
                  </select>
                </div>

                <div className="form-group">
                  <label htmlFor="name">Tn</label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    className="form-control"
                    placeholder="Nhp tn ca bn"
                    value={formData.name}
                    onChange={handleChange}
                    required
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="quantity">S lng</label>
                  <input
                    type="number"
                    id="quantity"
                    name="quantity"
                    className="form-control"
                    min="1"
                    max="10"
                    value={formData.quantity}
                    onChange={handleChange}
                    required
                  />
                </div>

                <div className="form-group text-center">
                  <button 
                    type="submit" 
                    className="btn btn-primary btn-submit"
                    disabled={submitting}
                  >
                    {submitting ? 'ang x l...' : 'Xc nhn tham d'}
                  </button>
                </div>

                <div className="form-group text-center">
                  <button 
                    type="button" 
                    className="btn btn-default"
                    onClick={onClose}
                  >
                    ng
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
}

export default React.memo(RegistModal);
